import { Component, OnInit, ViewChild} from '@angular/core';
import { OptionsInput } from '@fullcalendar/core';
import dayGridPlugin from '@fullcalendar/daygrid';
import interactionPlugin from '@fullcalendar/interaction';
import { CalendarComponent } from 'ng-fullcalendar';
import { EventsServices } from './services/events.service'


@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})


export class AppComponent implements OnInit {
  options: OptionsInput;
  eventsModel: any;  
  showModal: boolean;
  title: string;
  date:string;
  place:string;
  image:any;
  url:string;
  calendarOptions: OptionsInput;
  public response: any;
  public events = [];
  public atrib = [];
  public id = '';
  public datas = [];
  @ViewChild('fullcalendar') fullcalendar: CalendarComponent;
 

  constructor(
    private _requestService: EventsServices
    ) {

    console.log("Got to the service!");

  }
  ngOnInit() {
    this._requestService.getAll().subscribe(response =>{
    this.events = response['data'];
    //console.log(this.events);
    for(let i in this.events){
      this.atrib.push(this.events[i]['attributes']);
      if(this.atrib[i].field_event_date!= null){
        //change 'date' if you want display full date
        this.datas.push({'title': this.atrib[i].title , 'date': this.atrib[i].field_event_date})
      }
    }
    //console.log(this.datas);

    this.options = {
      editable: true,
      events: this.datas
    ,
      customButtons: {
        myCustomButton: {
          text: 'custom!',
          click: function() {
            alert('clicked the custom button!');
          }
        }
      },
      header: {
        left: 'prev,next today myCustomButton',
        center: 'title',
        right: 'dayGridMonth,timeGridWeek,timeGridDay'
      },
      plugins: [ dayGridPlugin, interactionPlugin ]
    };
    });
  }
  eventClick(model) {
    //filtro por el id
    this._requestService.getAll().subscribe(response =>{
      this.events = response['data'];
      for(let i in this.events){
        this.atrib.push(this.events[i]['attributes']);
        if(this.atrib[i]['title'] == model['event']['title']){
          this.id = this.events[i]['id'];
        }
      }
     //cojo el evento con el mismo id del model
      this._requestService.getEvent(this.id).subscribe(response =>{
        model = response['data'];
        console.log(model);
        this.title = model['attributes']['title'];
        this.date = this.DateOf(model['attributes']['field_event_date']);
        this.place = model['attributes']['field_place']['value'];
        this.url = model['attributes']['drupal_internal__nid'];
        console.log(this.url);
      });   
 
    });
        this.showModal = true;
      }
        hide()
      {
        this.showModal = false;
        this.title = '';
        this.date = '';
        this.place = '';
        this.image = '';
        this.url = '';
      }
  
  eventDragStop(model) {
    console.log(model);
  }
  dateClick(model) {
    console.log(model.dateStr);
    return model.dateStr;
  }
  updateEvents() {
    this.eventsModel = [{
      title: 'Updaten Event',
      start: this.yearMonth + '-08',
      end: this.yearMonth + '-10'
    }];
  }
  get yearMonth(): string {
    const dateObj = new Date();
    return dateObj.getUTCFullYear() + '-' + (dateObj.getUTCMonth() + 1);
  }

  DateOf(val : string) : string {
    var date = val.split("T", 1);
    return date[0];
  }

}