import { Injectable } from '@angular/core';
import { HttpClient} from '@angular/common/http';
import { GLOBAL } from '../global';
import { IEvents } from '../interfaces/IEvents'
import { Observable } from 'rxjs';

@Injectable()
export class EventsServices {

    public event = {
        data: '',
        drupal_internal_nid: '',
        field_advertise_on: '',
        title: ''
    }

    constructor(private _http: HttpClient) {}

    getAll() : Observable<IEvents[]>{
        //console.log(this._http.get(GLOBAL.urlAPI));
        return this._http.get<IEvents[]>(GLOBAL.urlAPI);
    }

    getEvent(id: string) : Observable<IEvents[]>{
        console.log(GLOBAL.urlAPI + '/' + id);
        return this._http.get<IEvents[]>(GLOBAL.urlAPI + '/' + id);
    }

    populateDialog(ev){
        this.event = ev;
    }
}

